          transform: translateX(calc(50vw - 19em));
}

.toggle-checkbox {
  position: absolute;
  opacity: 0;
  height: 0;
  width: 0;
}

.toggle-slot {
  position: relative;
  height: 10em;
  width: 20em;
  border-radius: 10em;
  background-color: var(--secondColor);
  transition: background-color 250ms;
  -webkit-animation: var(--scaleFwdAnim);
          animation: var(--scaleFwdAnim);
}

