:root {
  --fontSize: calc(10px + (26 - 14) * ((100vw - 300px) / (1600 - 300)));
  /* --fontSize: 12px; */

  --primaryColor: #e8630a;
  --secondColor: #035397;
  --thirdColor: #fcd900;
  --fourthColor: #001e6c;
  --fifthColor: #6c1800;

  --bgInput: white;

  --textColor: white;

  --fontRoboto: 'Roboto', sans-serif;
  --fontSora: 'Sora', sans-serif;

  --bounceAnimTop: bounceAnimTop 2s ease 0s 1 normal forwards;
  --bounceAnimBottom: bounceAnimBottom 2s ease 0s 1 normal forwards;
  --bounceAnimLeft: bounceAnimLeft 2s ease 0s 1 normal forwards;
  --bounceAnimRight: bounceAnimRight 2s ease 0s 1 normal forwards;
  --dropInAnim: dropInAnim 2s ease 0s 1 normal forwards;
  --waveAnim: waveAnim 4s ease 2s 1 normal forwards;
  --scaleFwdAnim: scaleFwdAnim 4s ease-in 0s 1 normal forwards;

  --transition: all 1s ease 0s;
}

[data-theme='dark'] {
  --primaryColor: #2a1b3d;
  --secondColor: #d83f87;
  --thirdColor: #a4b3b6;
