.box-inserter {
  background-color: var(--secondColor);
  padding: 1rem;
  -webkit-animation: var(--bounceAnimLeft);
          animation: var(--bounceAnimLeft);
  transition: var(--transition);
  border-radius: 2rem;
}

.box-inserter h3 {
  color: var(--textColor);
  margin: 0;
  padding-bottom: 1rem;
  text-align: center;
  font-family: var(--fontSora);
}

.box-inserter > form {
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-template-rows: repeat(5, auto);
  grid-gap: 1rem;
  justify-content: space-around;
  align-items: center;
  justify-items: end;
}

.box-inserter > form :nth-child(1) {
  grid-column: 1;
  grid-row: 1;
}

.box-inserter > form :nth-child(2) {
  grid-column: 1;
  grid-row: 2;
}

.box-inserter > form :nth-child(3) {
  grid-column: 1;
  grid-row: 3;
}

.box-inserter > form :nth-child(4) {
  grid-column: 1;
  grid-row: 4;
}

.box-inserter > form :nth-child(5) {
  grid-column: 2;
  grid-row: 2;
  justify-self: stretch;
}
.box-inserter > form :nth-child(6) {
  grid-column: 2;
  grid-row: 3;
  justify-self: stretch;
}

.alert-maximum {
  grid-column: 1/ 3;
  justify-self: center;
  color: var(--thirdColor);
  margin: 0;
  font-size: 0.8rem;
}

.errors-inserter {
  color: var(--thirdColor);
  height: 0.8rem;
  font-size: 0.8rem;
  letter-spacing: 0.05rem;
  word-spacing: 0;
  transition: var(--transition);
}

/*  dark mode   light mode
  #d83f87       #e8630a
  #44318d       #035397
  #2a1b3d       #FCD900
  #e98074       #001e6c
  #a4b3b6       #6c1800
*/

:root {
  --fontSize: calc(10px + (26 - 14) * ((100vw - 300px) / (1600 - 300)));
  /* --fontSize: 12px; */

  --primaryColor: #e8630a;
  --secondColor: #035397;
  --thirdColor: #fcd900;
  --fourthColor: #001e6c;
  --fifthColor: #6c1800;

  --bgInput: white;

  --textColor: white;

  --fontRoboto: 'Roboto', sans-serif;
  --fontSora: 'Sora', sans-serif;

  --bounceAnimTop: bounceAnimTop 2s ease 0s 1 normal forwards;
  --bounceAnimBottom: bounceAnimBottom 2s ease 0s 1 normal forwards;
  --bounceAnimLeft: bounceAnimLeft 2s ease 0s 1 normal forwards;
  --bounceAnimRight: bounceAnimRight 2s ease 0s 1 normal forwards;
  --dropInAnim: dropInAnim 2s ease 0s 1 normal forwards;
  --waveAnim: waveAnim 4s ease 2s 1 normal forwards;
  --scaleFwdAnim: scaleFwdAnim 4s ease-in 0s 1 normal forwards;

  --transition: all 1s ease 0s;
}

[data-theme='dark'] {
  --primaryColor: #2a1b3d;
  --secondColor: #d83f87;
  --thirdColor: #a4b3b6;
  --fourthColor: #44318d;
  --fifthColor: #e98074;

  --textColor: white;
}

