.box-editor {
  background-color: var(--fourthColor);
  padding: 1rem;
  -webkit-animation: var(--bounceAnimRight);
          animation: var(--bounceAnimRight);
  transition: var(--transition);
  border-radius: 2rem;
}

.box-editor h3 {
  color: var(--textColor);
  margin: 0;
  padding-bottom: 1rem;
  text-align: center;
  font-family: var(--fontSora);
}

.box-editor > form {
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-template-rows: repeat(5, auto);
  grid-gap: 1rem;
  justify-content: space-around;
  align-items: center;
  justify-items: end;
}

.box-editor div {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.box-editor > form :nth-child(1) {
  grid-column: 1;
  grid-row: 1;
}

.box-editor > form :nth-child(2) {
  grid-column: 1;
  grid-row: 2;
}

.box-editor > form :nth-child(3) {
  grid-column: 1;
  grid-row: 3;
}

.box-editor > form :nth-child(4) {
  grid-column: 1;
  grid-row: 4;
}
.box-editor > form :nth-child(5) {
  grid-column: 1;
  grid-row: 5;
}

.box-editor > form :nth-child(6) {
  grid-column: 2;
  grid-row: 1;
  justify-self: stretch;
}

.box-editor > form :nth-child(7) {
  grid-column: 2;
  grid-row: 2/5;
  justify-self: stretch;
  align-self: stretch;
}

.box-editor > form :nth-child(8) {
  grid-column: 2;
  grid-row: 5;
  justify-self: stretch;
}

.errors-editor {
  color: var(--thirdColor);
  height: 0.8rem;
  font-size: 0.8rem;
  letter-spacing: 0.05rem;
  word-spacing: 0;
  transition: var(--transition);
}

.editor-boxNumber {
  height: 1rem;
  width: 10rem;
  color: var(--textColor);
}

.box-editor .selected-color {
  display: flex;
  align-items: center;
  justify-content: center;
}

/*  dark mode   light mode
  #d83f87       #e8630a
  #44318d       #035397
  #2a1b3d       #FCD900
  #e98074       #001e6c
  #a4b3b6       #6c1800
*/

:root {
  --fontSize: calc(10px + (26 - 14) * ((100vw - 300px) / (1600 - 300)));
  /* --fontSize: 12px; */

  --primaryColor: #e8630a;
  --secondColor: #035397;
  --thirdColor: #fcd900;
  --fourthColor: #001e6c;
  --fifthColor: #6c1800;

  --bgInput: white;

  --textColor: white;

  --fontRoboto: 'Roboto', sans-serif;
  --fontSora: 'Sora', sans-serif;

  --bounceAnimTop: bounceAnimTop 2s ease 0s 1 normal forwards;
  --bounceAnimBottom: bounceAnimBottom 2s ease 0s 1 normal forwards;
  --bounceAnimLeft: bounceAnimLeft 2s ease 0s 1 normal forwards;
  --bounceAnimRight: bounceAnimRight 2s ease 0s 1 normal forwards;
  --dropInAnim: dropInAnim 2s ease 0s 1 normal forwards;
  --waveAnim: waveAnim 4s ease 2s 1 normal forwards;
  --scaleFwdAnim: scaleFwdAnim 4s ease-in 0s 1 normal forwards;

  --transition: all 1s ease 0s;
}

[data-theme='dark'] {
  --primaryColor: #2a1b3d;
  --secondColor: #d83f87;
  --thirdColor: #a4b3b6;
  --fourthColor: #44318d;
  --fifthColor: #e98074;

  --textColor: white;
}

